<template>
  <div v-loading='loading'>
    <cms-breadcrumb to="/advert/position/list" type='list' @click="rest">
      <li>{{$route.name}}</li>
    </cms-breadcrumb>
    <div class="cms-form-layout">
      <cms-prompt :collapse='false'>
        <li>带 * 为必填项</li>
      </cms-prompt>
      <el-form ref="form" :rules="rules" :model="formData" label-width="210px">
        <el-form-item label="广告位名称" prop="positionName" :error='errorMessage["positionName"]'>
          <el-input v-model="formData.positionName" maxlength="100"></el-input>
        </el-form-item>
         <el-form-item v-if="type==='save'" label="广告位标识" prop="positionCode" :error='errorMessage["positionCode"]' >
           <el-input v-model="formData.positionCode" maxlength="100"></el-input>
        </el-form-item>
        <el-form-item v-else label="广告位标识">
          <span>{{formData.positionCode}}</span>
        </el-form-item>
         <el-form-item label="是否启用" prop="isEnable" :error='errorMessage["isEnable"]'>
            <cms-switch v-model="formData.isEnable" ></cms-switch>
        </el-form-item>
         <el-form-item label="说明" prop="remark" :error='errorMessage["remark"]'>
          <el-input v-model="formData.remark" type="textarea" :rows="4" maxlength="200"></el-input>
        </el-form-item>
      </el-form>
      <div class="cms-form-footer">
        <cms-button @click="save(false)" v-color v-if="type=='save'">保存并新增</cms-button>
        <cms-button @click="save(true)" v-color v-if="type=='save'">保存</cms-button>
        <cms-button type='rest' @click="rest" v-if="type=='save'">重置</cms-button>
        <cms-button @click="update" v-color v-if="type=='update'">修改</cms-button>
      </div>
    </div>
  </div>
</template>

<script>
import rules from '@/rules/index.js'
import form from '@/mixins/form.js'
import { advertPositionSave, advertPositionUpdate, advertPositionGet } from '@/api/marketing/index.js'
var defaultData = {
  'positionName': '',
  'positionCode': '',
  'isEnable': true,
  'remark': ''
}
export default {
  mixins: [form],
  data() {
    return {
      formData: Object.assign({}, defaultData),
      rules: {
        'positionName': [rules.required('请填写广告版位名称')],
        'positionCode': [rules.required('请填写广告位标识')]
      }
    }
  },
  methods: {
    getFormData(id) { // 获取信息
      advertPositionGet({ id: id }).then(res => {
        if (res.code === 200) {
          this.formData = res.data
        }
      })
    },
    update() {
      delete this.formData.sysAds
      this.updateForm(advertPositionUpdate, this.formData, '/advert/position/list', 'list')
    },
    save(state) {
      this.saveForm(advertPositionSave, this.formData, state, '/advert/position/list', 'list')
    }
  },
  created() {
    if (this.type === 'update') {
      this.getFormData(this.id)
    }
  }
}
</script>

<style>
</style>



